<template>
  <v-container fill-height>
    <v-row class="text-center" align="center" justify="center">
      <v-col cols="12">
        <v-card width="500" class="pa-5 text-center d-inline-block">
          <v-card-title class="px-0">Payment</v-card-title>
          <v-form ref="form" v-model="valid">
            <v-text-field
              v-model="referenceNo"
              :rules="emailRules"
              label="Reference No"
              required
            ></v-text-field>

            <v-text-field
              v-model="value"
              :rules="[rules.required]"
              name="input-10-2"
              label="Value"
            ></v-text-field>

            <v-switch
              v-model="switch1"
              :label="`Monthly: ${switch1.toString()}`"
            ></v-switch>

            <v-btn :disabled="!valid" color="success" class="mr-4" @click="send" block>
              Send
            </v-btn>

          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "../api/index";
export default {
  name: "Login",
  data: () => ({
    switch1: true,
    valid: true,
    name: "",
    nameRules: [
      (v) => !!v || "Name is required",
      (v) => (v && v.length <= 10) || "Name must be less than 10 characters",
    ],
    referenceNo: "",
    emailRules: [
      (v) => !!v || "E-mail is required"
    ],
    select: null,
    items: ["Item 1", "Item 2", "Item 3", "Item 4"],
    checkbox: false,
    value: "",
    rules: {
      required: (value) => !!value || "Required.",
      min: (v) => v.length >= 8 || "Min 8 characters",
    },
  }),
  methods: {
    validate() {
      this.$refs.form.validate();
    },
    reset() {
      this.$refs.form.reset();
    },
    send() {
      axios
        .post("/api/v1/transaction", {
          ref: this.referenceNo,
          value: this.value,
          isMonthly: this.switch1,
        })
        .then((user) => {
          this.$router.push({path: "/"})
          console.log(user)
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
